<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list 
            ref="tgList" 
            :listType="['List']" 
            :IsShowAction="false" 
            :showCheckBox="true" 
            ModuleName="BillingRule"
            SubModuleCode="TemplateListing" 
            IdentityColumn="INVOICE_TEMPLATE_ID" 
            :HeaderText="$t('Manage') + ' ' + $t('InvoiceTemplate')" 
            :ListData="ListingData"
            :HeaderData="Headers" 
            :widgets="widgets" 
            :HeaderButtons="Buttons" 
            :TotalRecords="TotalRecords"
            :RenderRowActionMethod="renderActions" 
            :ListDataCallBackFunction="FetchTemplates" 
            :SortExp="SortExp"
            :SortBy="SortBy" 
            :NorecordfoundText="$t('NoRecordfound')" 
            v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" 
            v-on:SortdataButtonClick="sortdata"
            :LegendArray="LegendArray" 
            v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:ActionButtonClick="actionButtonClick"
            :SearchFields="leftSearchFields"
            SearchPosition="top"
        >
            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'INVOICE_TEMPLATE_NAME'">
                    <a href="javascript:;" @click="editTemplate(data.row)" :title="$t('ClickHereToViewThis').replace('{0}', data.row.INVOICE_TEMPLATE_NAME)">
                        {{ data.row.INVOICE_TEMPLATE_NAME }}
                    </a>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'CREATED_ON'">
                    <span v-if="data.row.CREATED_ON" :title="data.row.CREATED_ON">
                        {{ formatDate(data.row.CREATED_ON) }}
                    </span>
                    <span v-else>--</span>
                </template>
            </template>
        </tg-list>
    </section>
</template>

<script>
import  { locateBillingService } from '../../../services/locateBillingService'
import Schema from './TemplateListingSchema';

export default {
    name: "TemplateListing",
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            LegendArray: [],
            SortBy: '',
            SortExp: '',
            CurrentPage: 1,
            TotalRecords: 0,
            ListingData: [],
            Buttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: Schema.headerSchema,
            searchCondition: "",
            setupId: null
        }
    },
    created: function () {
        var vm = this;
        vm.setupId = this.$route.params.setupId || null;
        vm.Headers.forEach(element => {
            element.DISPLAY_NAME = vm.$t(element.DISPLAY_NAME);
        });
        vm.FetchTemplates();
    },
    methods: {
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    this.searchCondition = value.searchConditionJson;
                } else {
                    this.searchCondition = value.searchCondition || "";
                }
            } else {
                this.searchCondition = "";
            }
            this.FetchTemplates();
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            // Check if template can be deleted (TemplateCount > 0 means it's in use)
            if (row.TEMPLATE_COUNT > 0) {
                // Disable checkbox for templates in use
                return rtnActionArray;
            }
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchTemplates();
        },
        actionButtonClick: function (callbackfunction, item) {
            // Row actions if any
        },
        commonHeaderButtonClick: function (event) {
            switch (event.callbackfunction) {
                case "AddTemplate":
                    this.addTemplate();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "BacktoList":
                    this.BacktoList();
                    break;

            }
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            if(this.SortBy=='INVOICE_TEMPLATE_NAME'){
                this.SortBy='TEMP';
            }else if(this.SortBy=='SETUP_NAME'){
                this.SortBy='SETUPNAME'
            }else{
                 this.SortBy = sortBy;
            }
            if (this.SortExp == "") {
                this.SortExp = "ASC";
            } else if (this.SortExp == "ASC") {
                this.SortExp = "DESC";
            } else {
                this.SortExp = "ASC";
            }
            this.FetchTemplates();
        },
        FetchTemplates: function () {
            var vm = this;
            vm.isLoading = true;
            var url = `pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            
            if (vm.setupId) {
                url += `&setupId=${vm.setupId}`;
            }
            
            if (vm.searchCondition) {
                url += `&${vm.searchCondition}`;
            }
            locateBillingService.GetTemplateListing(url).then((response) => {
                vm.isLoading = false;
                 if(response.data.result){
                    vm.ListingData=JSON.parse(response.data.result)
                }
                if (vm.ListingData  && vm.ListingData.length > 0) {
                    vm.TotalRecords = vm.ListingData[0].TOTAL_RECORDS || vm.ListingData[0].TOTALRECORDS || 0;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.Buttons[1].isdisabled = true;
                } else {
                    vm.ListingData = [];
                    vm.TotalRecords = 0;
                }
            }).catch((error) => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
                console.error('Error fetching templates:', error);
            });
            
            // Placeholder - remove when API is ready
            setTimeout(() => {
                vm.isLoading = false;
                vm.ListingData = [];
                vm.TotalRecords = 0;
            }, 100);
        },
        formatDate(dateString) {
            if (!dateString) return '';
            if (dateString instanceof Date) {
                return dateString.toLocaleDateString();
            }
            const date = new Date(dateString);
            if (isNaN(date.getTime())) return dateString;
            return date.toLocaleDateString();
        },
        addTemplate() {
            var vm = this;
            var route = '/BillingRule/Template';
            if (vm.setupId) {
                route += `?setupId=${vm.setupId}`;
            }
            vm.$router.push(route);
        },
        editTemplate(row) {
            var vm = this;
            var route = `/BillingRule/Template?templateId=${row.INVOICE_TEMPLATE_ID}`;
            if (vm.setupId) {
                route += `&setupId=${vm.setupId}`;
            }
            if (vm.searchCondition) {
                route += `&${vm.searchCondition}`;
            }
            vm.$router.push(route);
        },
         commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Tag")), function () {
                    var url = `ids=${val}&tablename=TALYGEN_BE_INVOICE_TEMPLATE&primaryKey=INVOICE_TEMPLATE_ID`;
                    locateBillingService.DeleteItems(url).then(function (response) {
                        debugger
                        if (response.status == 200) {
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Template')]), "success", true, vm.$t("Alert"));
                            vm.FetchTemplates();
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Template')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        },
        BacktoList(){
            var vm=this;
             vm.$router.push({ name: 'ImportFormatListing'});
        }
    }
}
</script>

<style scoped>
</style>

